const PACKAGE_NAME = '@wix/auto_sdk_benefit-programs_programs';

/**
 * Creates a program for the specified beneficiary. Creating a program is also referred to as provisioning a program.
 *
 * You can provision a program from a program definition or a pool definition:
 * - **Program definition**: The provisioned program has pools that are created from the pool definitions in the program definition.
 * - **Pool definition**: The provisioned program has 1 pool that is created from the pool definition.
 */
export function provisionProgram(payload: object): RequestOptionsFactory<any> {
  function __provisionProgram({ host }: any) {
    const metadata = {
      entityFqdn: 'wix.benefit_programs.v1.program',
      method: 'POST' as any,
      methodFqn:
        'wix.benefit_programs.v1.program.ProgramService.ProvisionProgram',
      packageName: PACKAGE_NAME,
      url: resolveWixBenefitProgramsV1ProgramProgramServiceUrl({
        protoPath: '/v1/programs/provision',
        data: payload,
        host,
      }),
      data: payload,
      transformResponse: (payload: any) =>
        transformPaths(payload, [
          {
            transformFn: transformRESTTimestampToSDKTimestamp,
            paths: [
              { path: 'program.createdDate' },
              { path: 'program.updatedDate' },
            ],
          },
        ]),
    };

    return metadata;
  }

  return __provisionProgram;
}

