export function updateProgram(httpClient: HttpClient): UpdateProgramSignature {
  return (
    _id: string,
    program: NonNullablePaths<UpdateProgram, `revision`, 2>
  ) =>
    universalUpdateProgram(
      _id,
      program,
      // @ts-ignore
      { httpClient }
    );
}

interface UpdateProgramSignature {
  /**
   * Updates a program.
   *
   * Each time the program definition is updated,
   * `revision` increments by 1.
   * The current `revision` must be passed when updating the program definition.
   * This ensures you're working with the latest program definition
   * and prevents unintended overwrites.
   * @param - Program ID.
   * @returns Updated program.
   */
  (
    _id: string,
    program: NonNullablePaths<UpdateProgram, `revision`, 2>
  ): Promise<
    NonNullablePaths<
      Program,
      | `status`
      | `beneficiary.anonymousVisitorId`
      | `beneficiary.memberId`
      | `beneficiary.wixUserId`
      | `programDefinition._id`,
      3
    >
  >;
}

